import PerfectPitch from "./components/Test.vue";
import SignUp from "./components/SignUp.vue";
import { createRouter, createWebHashHistory } from 'vue-router';

const routes = [
    {
        name: 'Test',
        component: PerfectPitch,
        meta: { bodyClass: "test-page-background" },
        path: '/',
    },
    {
        name: 'SignUp',
        component: SignUp,
        path: '/sign-up',
    },
];

const router = createRouter({
    history: createWebHashHistory(),
    routes,
})

router.beforeEach((to, from, next) => {
    if (from.meta.bodyClass) {
      document.body.classList.remove(from.meta.bodyClass);
    }
    if (to.meta.bodyClass) {
      document.body.classList.add(to.meta.bodyClass);
    }
    next();
  });

export default router;