<template>
<html lang="en"> 
<head>
    <meta charset="UTF-8">
    <meta name="viewport" content="width=device-width, initial-scale=1.0">
    <title>Perfect Pitch Chord Training</title>
</head>
<body>
    <div class="container">
        <header>
            <h1 class="text-center">Perfect Pitch Chord Practice</h1>
        </header>
        <div class="text-center">
            <div class="progress" v-show="started">
                <span class="progress-text">
                    Question Number<br>
                    {{ currentQuestion }} / {{ numQuestions }}
                </span>
                <div id="bar" v-show="started">
                    <div id="inbar" :style="{ width: progressWidth() }">
                        <div id="corbar" :style="{ width: progressRightWidth() }"></div>
                    </div>
                </div>
                <span class="progress-text">
                    Right Answer<br>
                    {{ rightAnswer }} / {{ numQuestions }}
                </span>
            </div>
            <div class="modal" v-show="finish">
                <div class="message">
                    <h2>Ok You Just finished</h2>
                    <p>Your Score: {{ rightAnswer }} / {{ numQuestions }}<br>{{ finishmes }}</p>
                    <button @click="restart" class="option">Restart</button>
                    <button @click="closePopup" class="option">Close</button>
                </div>
            </div>
            <button ref="opt1" @click="nextQuestion()" v-show="started" class="option">{{ currentQuestion === 0 ? 'Start Listen' : 'Hear Next' }}</button>
            <button ref="opt2" @click="replay()" v-show="started" class="option">Hear Again</button>
            <button ref="opt3" @click="indi()" v-show="started" class="option">Play Individial Note</button>
            <button ref="opt4" @click="display()" v-show="started" class="option">Display On Keyboard</button>
            <ul class="keys">
                <li
                    v-for="key in keys"
                    :key="key.name"
                    :class="key.class"
                    @click="playTune(key.name)"
                    :data-name="key.name"
                >
                    <span>{{ key.label }}</span>
                </li>
            </ul>
            <h2 class="inside" v-show="started">Tonic</h2>
            <div class="choices-container" v-show="started">
                <button
                    v-for="tonic in tonics"
                    :key="tonic.name"
                    :class="[tonic.class, { 'active-choice': tonic.selected }]"
                    @click="selectTonic(tonic.name)"
                >
                    {{ tonic.name }}
                </button>
            </div>
            <h2 class="inside" v-show="started">Quality</h2>
            <div class="choices-container" v-show="started">
                <button
                    v-for="quality in qualities"
                    :key="quality.name"
                    :class="[quality.class, { 'active-choice': quality.selected }]"
                    @click="selectQuality(quality.name)"
                >
                    {{ quality.name }}
                </button>
            </div>
            <div v-if="!started">
                <h2>Select Number Of Questions</h2>
                <input type="number" class="start dis" v-model="numQuestions" min="0" max="500" />
                <br>
                <button class="start dis" @click="startGame">Start</button>
            </div>
        </div>
    </div>
</body>
</html>
</template>  
  
<script>
export default {
    name : 'PerfectPitch',
    mounted(){
        let user = localStorage.getItem('user-info');
        console.log("User Info:", user);
        /*if(!user){
            this.$router.push({name:'SignUp'})
        }*/
        document.body.classList.add("test-page-background");
    },
    beforeUnmount() {
        document.body.classList.remove("test-page-background");
    },
    data() {
        return {
            started: false,
            numQuestions: 0, 
            currentQuestion: 0,
            userTonic: null, 
            userQuality: null,
            finish: false,
            finishmes: null,
            tonics: [
                    { name: "A", class: "choices", selected: false}, 
                    { name: "A#", class: "choices", selected: false}, 
                    { name: "B", class: "choices", selected: false}, 
                    { name: "C", class: "choices", selected: false},
                    { name: "C#", class: "choices", selected: false}, 
                    { name: "D", class: "choices", selected: false}, 
                    { name: "D#", class: "choices", selected: false}, 
                    { name: "E", class: "choices", selected: false}, 
                    { name: "F", class: "choices", selected: false}, 
                    { name: "F#", class: "choices", selected: false}, 
                    { name: "G", class: "choices", selected: false}, 
                    { name: "G#", class: "choices", selected: false}],
            qualities: [
                    { name: "Major", class: "choices", selected: false}, 
                    { name: "Minor", class: "choices", selected: false}, 
                    { name: "Diminished", class: "choices", selected: false}, 
                    { name: "Augmented", class: "choices", selected: false}, 
                    { name: "Dominant Seventh", class: "choices", selected: false},
                    { name: "Minor Seventh", class: "choices", selected: false}, 
                    { name: "Major Seventh", class: "choices", selected: false}],
            keys: [
                { name: "C3", label: "C", class: "key w", sound: "C3.wav"},
                { name: "C_3", label: "C#", class: "key b", sound: "C_3.wav"},
                { name: "D3", label: "D", class: "key w", sound: "D3.wav"},
                { name: "D_3", label: "D#", class: "key b", sound: "D_3.wav"},
                { name: "E3", label: "E", class: "key w", sound: "E3.wav"},
                { name: "F3", label: "F", class: "key w", sound: "F3.wav"},
                { name: "F_3", label: "F#", class: "key b", sound: "F_3.wav"},
                { name: "G3", label: "G", class: "key w", sound: "G3.wav"},
                { name: "G_3", label: "G#", class: "key b", sound: "G_3.wav"},
                { name: "A3", label: "A", class: "key w", sound: "A3.wav"},
                { name: "A_3", label: "A#", class: "key b", sound: "A_3.wav"},
                { name: "B3", label: "B", class: "key w", sound: "B3.wav"},
                { name: "C4", label: "C", class: "key w", sound: "C4.wav"},
                { name: "C_4", label: "C#", class: "key b", sound: "C_4.wav"},
                { name: "D4", label: "D", class: "key w", sound: "D4.wav"},
                { name: "D_4", label: "D#", class: "key b", sound: "D_4.wav"},
                { name: "E4", label: "E", class: "key w", sound: "E4.wav"},
                { name: "F4", label: "F", class: "key w", sound: "F4.wav"},
                { name: "F_4", label: "F#", class: "key b", sound: "F_4.wav"},
                { name: "G4", label: "G", class: "key w", sound: "G4.wav"},
                { name: "G_4", label: "G#", class: "key b", sound: "G#4.wav"},
                { name: "A4", label: "A", class: "key w", sound: "A4.wav"},
                { name: "A_4", label: "A#", class: "key b", sound: "A_4.wav"},
                { name: "B4", label: "B", class: "key w", sound: "B4.wav"}
            ],
            ListofChords: [
                [4, 7], [-8, -5], [-5, 4], [3, 7], [-9, -5], [-5, 3], [3, 6], [-9, -6], [-6, 3],
                [4, 8], [-8, -4], [-4, 4], [4, 7, 10], [-8, -5, -2], [-5, -2, 4], [-2, 4, 7],
                [3, 7, 10], [-9, -5, -2], [-5, -2, 3], [-2, 3, 7], [4, 7, 11], [-8, -5, -1],
                [-5, -1, 4], [-1, 4, 7]
            ]
        };
    },
    methods: {
    startGame() {
        this.started = true;
        this.currentQuestion = 0;
        this.rightAnswer = 0;
        this.finish = false;
    },
    restart(){
        this.started = false;
        this.finish = false;
    },
    playTune(keySound) {
    try {
        const context = require.context('../assets/Pitch', false, /\.wav$/); 
        const soundPath = context(`./${keySound}.wav`); 
        const audio = new Audio(soundPath);
        audio.play();
    } catch (error) {
        console.error(`Failed to load sound file: ${keySound}`, error);
    }
    },
    playTune_chord(keySound) {
    try {
        const context = require.context('../assets/Pitch', false, /\.wav$/); 
        const soundPath = context(`./${keySound}.wav`); 
        const audio = new Audio(soundPath);
        audio.volume = 0.45;
        audio.play();
    } catch (error) {
        console.error(`Failed to load sound file: ${keySound}`, error);
    }
    },
    getRandomInt(max) {
        return Math.floor(Math.random() * max);
    },
    nextQuestion() {
        this.tonics.forEach(tonic => tonic.selected = false);
        this.qualities.forEach(quality => quality.selected = false);
        if (this.currentQuestion < this.numQuestions) {
            this.currentQuestion++;
            this.play = [];
            let ran_ton = this.getRandomInt(24) + 1;
            let ran_qua = this.getRandomInt(this.ListofChords.length);
            while (
                ran_ton + this.ListofChords[ran_qua][0] < 1 ||
                ran_ton + this.ListofChords[ran_qua][this.ListofChords[ran_qua].length - 1] > 24
            ) {
                ran_ton = this.getRandomInt(24) + 1;
                ran_qua = this.getRandomInt(this.ListofChords.length);
            }

            this.selectedTonic = this.keys[ran_ton - 1].label;
            this.selectedQuality = this.checkQuality(ran_qua);

            const tonicKey = this.keys[ran_ton - 1].name;
            this.play.push(ran_ton - 1); 
            this.playTune_chord(tonicKey);
            this.ListofChords[ran_qua].forEach((interval) => {
                const chordKey = this.keys[ran_ton - 1 + interval].name;
                this.play.push(ran_ton - 1 + interval);
                this.playTune_chord(chordKey);
            });
            console.log(this.currentQuestion, this.numQuestions);
        }
        else{
            this.popup();
        }
    },
    replay() {
    if (this.play && this.play.length > 0) {
        this.play.forEach((keyIndex) => {
            const soundFile = this.keys[keyIndex].name;
            this.playTune_chord(soundFile);
        });
    } else {
        console.error("No sound has been played yet to replay.");
    }
    },
    indi() {
    let delay = 0;
    if (this.play && this.play.length > 0) {
        const sortedPlay = this.play.slice().sort((a, b) => a - b);
        sortedPlay.forEach((keyIndex) => {
            const soundFile = this.keys[keyIndex].name;
            setTimeout(() => {
                this.playTune_chord(soundFile);
            }, delay);
            delay += 500;
        });
    } else {
        console.error("No sound has been played yet to replay.");
    }
    },
    display() {
    let delay = 0;
    if (this.play && this.play.length > 0) {
        const sortedPlay = this.play.slice().sort((a, b) => a - b);
        sortedPlay.forEach((keyIndex) => {
            const soundFile = this.keys[keyIndex].name;
            const keyElement = document.querySelector(`.keys li[data-name="${soundFile}"]`);
            setTimeout(() => {
                keyElement.classList.add("playing");
                this.playTune_chord(soundFile);
                setTimeout(() => {
                    keyElement.classList.remove("playing");
                }, 500);
            }, delay);
            
            delay += 500;
        });
    } else {
        console.error("No sound has been played yet to replay.");
    }
    },
    checkQuality(num) {
    if (num >= 20) {
        return this.qualities[6].name;
    } else if (num >= 16) {
        return this.qualities[5].name;
    } else if (num >= 12) {
        return this.qualities[4].name;
    } else if (num >= 9) {
        return this.qualities[3].name;
    } else if (num >= 6) {
        return this.qualities[2].name;
    } else if (num >= 3) {
        return this.qualities[1].name;
    } else {
        return this.qualities[0].name;
    }
    },
    selectTonic(tonicName) {
    this.tonics.forEach((tonic) => {
        tonic.selected = tonic.name === tonicName;
    });
    this.userTonic = tonicName;
    this.checkAnswer();
    },
    selectQuality(qualityName) {
    this.qualities.forEach((quality) => {
        quality.selected = quality.name === qualityName;
    });
    this.userQuality = qualityName;
    this.checkAnswer();
    },
    checkAnswer() {
    if (this.userTonic && this.userQuality) {
        if (this.selectedQuality === 'Augmented'){
            this.userTonic = this.aug_handle(this.userTonic);
            this.selectedTonic = this.aug_handle(this.selectedTonic);
        }
        if (this.userTonic === this.selectedTonic && this.userQuality === this.selectedQuality) {
            this.rightAnswer++;
            alert("Correct!");
        } else {
            alert("Fuck Off!");
        }
        if(this.userTonic && this.userQuality){
            this.popup();
        }
        this.nextQuestion();
        this.userTonic = null;
        this.userQuality = null;
    }
    },
    popup(){
        if (this.currentQuestion >= this.numQuestions) {
            this.finish = true;
            if (this.rightAnswer <= this.numQuestions/3){
                this.finishmes = 'You Suck lol'
            }else if (this.rightAnswer > this.numQuestions/3 && this.rightAnswer <= this.numQuestions/3 * 2){
                this.finishmes = "Go Practice more even you can't"
            }else{
                this.finishmes = 'I Hate You, You Perfect Pitch bastard'
            }
        }
    },
    aug_handle(root) {
    if (root === 'E' || root === 'G#') {
        return 'C';
    } else if (root === 'F' || root === 'A') {
        return 'C#';
    } else if (root === 'F#' || root === 'A#') {
        return 'D';
    } else if (root === 'G' || root === 'B') {
        return 'D#';
    } else {
        return root;
    }
    },
    progressWidth() {
        return `${(this.currentQuestion / this.numQuestions) * 100}%`;
    },
    progressRightWidth() {
        return `${(this.rightAnswer / this.currentQuestion) * 100}%`;
    },
    closePopup() {
        this.finish = false;
    },
    }
}
</script>
  
<style>
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

body.test-page-background {
    background-color: #dedede;
}

body {
    background-color: #dedede;
}

.container {
    font-size: 20px;
    background-color: #dedede;
    margin-top: -15px;
}

.progress-text {
    font-size: 15px;
}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.4);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 200;
    animation-name: bg;
    animation-duration: 1s;
}

.message {
    background-color: rgba(255, 255, 255);
    padding: 3rem;
    border-radius: 5vh;
    animation-name: test;
    animation-duration: 1s;
}

@keyframes test {
    from {
        transform: scale(0.5);
        opacity: 0;
    }
    to {
        transform: scale(1);
        opacity: 1;
    }
}

@keyframes bg {
    from {background-color: rgba(0, 0, 0, 0);}
    to {background-color: rgba(0, 0, 0, 0.4);}
}

.message h2 {
    margin-top: -0.4rem;
}

.message p {
    margin-bottom: 1vh;
}

h1, .h1 {
    margin-top: 20px;
    font-size: 2em;
    display: block;
    margin-bottom: 10px;
}

h2, .inside {
    margin-top: 10px;
    font-size: 1.7em;
    display: block;
    margin-bottom: 10px;
}

h2, .outside {
    margin-top: 30px;
    font-size: 1.7em;
    display: block;
    margin-bottom: 10px;
}

.text-center {
    text-align: center;
    justify-content: center;
}

.choices {
    color: #000000;
    background-color: #26ff00;
    border: 3px solid #008f07;
    margin: 5px;
    display: inline-block;
    margin-bottom: 0;
    font-weight: normal;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    background-image: none;
    white-space: nowrap;
    padding: 6px 12px;
    font-size: 16px;
    border-radius: 10px;
}

.option {
    color: #000000;
    background-color: #61daff;
    border: 3px solid #001f8f;
    margin: 5px;
    display: inline-block;
    margin-bottom: 0;
    font-weight: normal;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    background-image: none;
    white-space: nowrap;
    padding: 6px 12px;
    font-size: 20px;
    border-radius: 10px;
}

.keys {
    display: flex;
    margin-top: 5vh;
    justify-content: center;
    position: relative;
}

.keys .key{
    cursor: pointer;
    list-style: none;
    color: #000000;
    position: relative;
    user-select: none;
}

.keys .w{
    width: 5vw;
    height: 30vh;
    background: #ffffff;
    border: 2px solid #000000;
    border-radius: 1.5vh;
    position: relative;
}

.keys .w:hover:active{
    transform: translateY(5px);
    background: #e1e1e1;
}

.keys .w:hover{
    background: #eaeaea;
}

.keys .b:hover:active{
    transform: translateY(3px);
    background: #242424;
}

.keys .w.playing{
    transform: translateY(5px);
    background: #e1e1e1;
}

.keys .b.playing{
    transform: translateY(3px);
    background: #242424;
}

.keys .b:hover{
    background: #141414;
}

.keys .b{
    width: 3vw;
    height: 20vh;
    background: #000000;
    z-index: 2;
    margin: 0 -1.5vw;
    border: 2px solid #000000;
    border-radius: 1vh;
    margin-top: -3px;
    position: relative;
    color: #ffffff;
}

.keys .key span {
    position: absolute;
    bottom: 5%;
    transform: translateX(-50%);
    font-size: 1vw;
}

.start {
    color: #000000;
    background-color: #ff0000;
    border: 6px solid #8f0000;
    margin: 5px;
    display: inline-block;
    margin-top: 30px;
    font-weight: normal;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    background-image: none;
    white-space: nowrap;
    padding: 6px 12px;
    font-size: 40px;
    border-radius: 20px;
}

.start#input{
    color: #000000;
    background-color: #1aff003f;
    border: 6px solid #11ff00;
    margin: 5px;
    display: inline-block;
    margin-top: 30px;
    font-weight: normal;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    background-image: none;
    white-space: nowrap;
    padding: 6px 12px;
    font-size: 40px;
    border-radius: 20px;
}

.start:hover:active {
    background-color: rgb(186, 0, 0);
    border: 6px solid #8f0000;
}

.start:hover {
    background-color: rgb(222, 0, 0);
    border: 6px solid #8f0000;
}

.option:hover:active {
    background-color: #009dff;
    border: 3px solid #001f8f;
}

.option:hover {
    background-color: #52bdff;
    border: 3px solid #001f8f;
}

.choices:hover:active {
    background-color: #1aab00;
    border: 3px solid #008f07;
}

.choices:hover {
    background-color: #1fcc00;
    border: 3px solid #008f07;
}

.choices.active-choice {
    background-color: #1aab00;
    border: 3px solid #008f07;
}

#bar {
    position: relative;
    background-color: #ffff9a;
    border: 3px solid #ffb300;
    width: 70vw;
    height: 3vh;
    border-radius: 20px;
}

#inbar {
    height: 100%;
    background-color: #ffb300;
    border-radius: 20px;
}

#corbar {
    height: 90%;
    background-color: #00ff3c;
    border-radius: 18px;
}

.progress{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    margin-bottom: 20px;
}
</style>